import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';

import { TimeFormatDirective } from './directives/time-format.directive';

import { SeriesComponent } from './series.component';
import { AsignarNumeroComponent } from './asignar-numero/asignar-numero.component';
import { SeriesHeaderComponent } from './series-header/series-header.component';
import { CargarTiemposComponent } from './cargar-tiempos/cargar-tiempos.component';
import { ResultadosComponent } from './resultados/resultados.component';

const routes: Routes = [
  { 
    path: '', 
    /* resolve: { categoriaData: CategoriasResolver },  */
    component: SeriesComponent
  },
  { path: 'asignar-numero/:id', component: AsignarNumeroComponent },
  { path: 'cargar-tiempos/:id', component: CargarTiemposComponent },
  { path: 'resultados/:id', component: ResultadosComponent }
]; 

@NgModule({
  declarations: [
    TimeFormatDirective,
    SeriesComponent, 
    SeriesHeaderComponent,
    AsignarNumeroComponent,
    CargarTiemposComponent,
    ResultadosComponent
  ],
  imports: [
    CommonModule,
    FormsModule,
    RouterModule.forChild(routes)
  ],
  exports: [RouterModule]
})
export class SeriesModule { }
